.testimonials{
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
    
    align-items: center;
    font-family: 'Baloo Thambi 2';
font-style: normal;
font-weight: 400;
font-size: 56px;
line-height: 148.52%;
/* or 83px */


color: #D6809B;
    background-color: #E6CBAE;
}

.testimonial-wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 80px 0;
    width: 100%;
}
.testimonial-cards{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;

    align-items: flex-start;
    width: 80%;
    
}