.faqs-container{
    width: 80%;
    padding: auto;

    margin: auto;
}
.faq-header{
    font-family: 'Average';
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    line-height: 148.52%;
    padding-top: 15vh;
    color: #917196;
    text-align: center;
}
.faq-about{
    width: 45vw;
    margin:2vh auto;
    padding-bottom: 15vh;
    font-family: 'Average';
font-style: normal;
font-weight: 400;
font-size: 24px;
line-height: 28px;
/* or 117% */

text-align: center;

color: #917196;
    text-align: center;
}
.elongated-page{
    background: url('../../assets/mainBack.png') no-repeat;
    height: 1500px;
    background-size: contain;
    margin-top: -60px;
    
}
.faq-know-more section{
display: flex;
column-gap: 40px;
}
.know-more-text{
    font-family: 'Average';
font-style: normal;
font-weight: 400;
font-size: 42px;
line-height: 51px;
/* identical to box height */

display: flex;
align-items: center;
justify-content: center;
color: #000000;
}
.know-more-img img{
  height: 100px;
  width: 100px;
  border-radius: 50%;
}
.faq-know-more{
    display: flex;
    border: 2px solid black;
    justify-content: space-between;
    align-items: center;
    width: 76vw;
    border: 2px solid #917196;
filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
backdrop-filter: blur(2px);
/* Note: backdrop-filter has minimal browser support */
margin: 20vh auto;
border-radius: 30px;
padding: 20px;
}
.know-more-btn{
    font-family: 'Average';
font-style: normal;
font-weight: 400;
font-size: 32px;
line-height: 39px;
/* identical to box height */

display: flex;
align-items: center;
justify-content: center;
color: #FFFFFF;

text-align: center;
    background: #917196;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 37px;
width: 236px;

height: 70px;
}