.form-container{
    height: 125vh;
    width: 100vw;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;

}
.form-height{
    height: 1120px;
    background: url('../../assets/mainBackWithoutMandala.png');
    background-repeat: no-repeat;
    background-size: contain;
    margin-top: -2px;
}
.form-decors{
    position: absolute;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    width: 100vw;
    top: 120vh;
    z-index: -10;
}
.form-decors img{
    height: 80vh;

}