.qualification-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    width: 15vw;
    margin: 0 4vw;
    background: #FFFFFF;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 12px;
}
.qualification-card-image img{
    width: 15vw;
    height: 30vh;
}
.qualification-card-title{
    padding: 10px;
}
.qualification-card-body{
    padding: 5px 10px;
}