.form-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 90vh;
  width: 70vw;
  background: url('../../assets/formBack.png');
  background-size: cover;
  
  backdrop-filter: blur(2px);
  /* Note: backdrop-filter has minimal browser support */
  
  border-radius: 27px;
}
.form {
  
  width: 40%;
  padding: 0 8vw;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.form h1 {
  font-family: "Average";
  font-style: normal;
  font-weight: 400;
  font-size: 65px;
  line-height: 91px;

  color: #917196;
}
.form input {
  font-family: "Average";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 39px;
  /* identical to box height */
  padding: 0 12px;

  color: #d6809b;

  height: 59px;
  border: 1px solid #d6809b;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 21px;
  margin: 15px 0;
}
input:nth-child(5) {
  height: 20vh;
  padding-bottom: 10vh;
}
.form button {
  font-family: "Average";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 39px;
  /* identical to box height */

  color: #ffffff;
  background: #917196;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 37px;
  height: 60px;
margin-top: 10px;
margin-bottom: 25px;
  width: 170px;
  align-self: flex-end;
}
