
.footer{
    background: url('../../assets/footerBack.png');
    width: 100%;
    height: 310px;
background-size: cover;
position: relative;
display: flex;
flex-direction: column;
align-items: flex-end;
}
.socials{ 
    font-family: 'Monda';
font-style: normal;
font-weight: 400;
font-size: 20px;
line-height: 33px;
/* identical to box height */

text-transform: capitalize;

color: #FFFFFF;

    position: absolute;
    top: 100px;
    left: 55px;
}
.follow-icons{
    display: flex;
    
}
.follow-icons img{
  padding-top:5px ;
  padding-right:15px ;
}
.footer-links{
    position: relative;
    top: 100px;
    right: 100px;
    display: flex;
    width:23vw;
    justify-content: space-between;
    
}
.foot-nav-links{
    font-family: 'Monda';
    color: #FFFFFF;
    font-style: normal;
    font-weight: 400;
    font-size: 250px;
    line-height: 33px;
    text-align: center;
    text-transform: capitalize;
    font-size: 23px;
}
.footer-btn{
    font-family: 'Average';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    height: 50px;
    color: #D6809B;
    background: #FAF5EF;
    border-radius: 37px;
width: 150px;
}
.further-contact h1{
    font-family: 'Monda';
    color: #FFFFFF;
    font-style: normal;
    font-weight: 400;
    font-size: 250px;
    line-height: 33px;
    text-transform: capitalize;
    font-size: 23px;

}