.testimonial-card-wrapper{
  position: relative;
  height: 250px;
  
}
.circular-img img{
    position: absolute;
z-index: 10000;
    width: 112.94px;
height: 112.94px;
border-radius: 50%;
}
.testimonial-text{
    position: absolute;
    background-color: red;
    height: 200px;
    bottom: 0;
    right: 0;
    width: 340px;
    
    /* display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; */
        
}
.testimonial-text p{
    max-width: 250px;
    padding-left: 80px;


}
.testimonial-text img:nth-child(1){
    width: 39.77px;
    height: 27.83px;
   padding-left: 85px;
   padding-top: 20px;

}
.testimonial-text img:nth-child(3){
    width: 39.77px;
    height: 27.83px;
    padding: 0 250px;
}