.benefits{
    background-color: #D6809B;
    width: 100%;
    display: flex;
    background-repeat: no-repeat;
    background-size: 100% 700px;
    height: 160vh;
}
.benefitsContainer{
    padding: 80px 0;
    width: 100%;

}
.cardContainer{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 2rem;
    padding:70px 80px ;
    align-items: flex-start;

    
}
.benefitsTitle{
    font-family: 'Averia Serif Libre';
    font-style: normal;
    font-weight: 400;
    font-size: 56px;
    line-height: 68px;
   width: 100%;
text-align:  center;
    
    
    color: #E6CBAE;
    
}