.qualification{
height: 95vh;
background: url('../../assets/leftQualif.svg');
background-repeat: no-repeat;
background-size: 400px;
display: flex;
align-items: center;
flex-direction: column;
padding-top: 10vh;

}
.qualification-title{
    padding: 20px 0;
    font-family: 'Averia Serif Libre';
    font-style: normal;
    font-weight: 400;
    font-size: 56px;
    line-height: 68px;
    /* identical to box height */
    
    letter-spacing: 0.02em;
    
    color: #917196;
    
        
}
.cards{
    display: flex;
    flex-direction: row;
    padding: 10vh 10vw;

}